.feature-v6__item {
  margin: 20px; }
  .feature-v6__item img {
    display: block;
    width: 100%;
    margin-bottom: var(--space-sm); }

.main-image {
  width: 100%;
  height: 20em;
  overflow: hidden;
  background-attachment: fixed;
  background-position: center;
  background-repeat: no-repeat;
  background-size: cover;
  background-image: url("https://sun9-19.userapi.com/c855416/v855416336/1c042f/2WuoWb63mw4.jpg"); }
  .main-image img {
    transform: translate(0, -20%);
    width: 100%; }
